import React, {useState} from 'react'
export default function TextForm(props) {

    function speakMessage(message) {
        if ('speechSynthesis' in window) {
          const speech = new SpeechSynthesisUtterance(message);
          speech.lang = 'en-US'; // Set the language, adjust as needed
          window.speechSynthesis.speak(speech);
        } else {
          console.log("Sorry, your browser doesn't support speech synthesis.");
        }
      }
      
    const handleUpperClick = ()=>{
        console.log("button is clicked"+ text);
        let newText = text.toUpperCase();
        setText(newText)
        props.showAlert("Converted to upper case","success");
        speakMessage("converted to upper case");
    }
    const handleLoClick = ()=>{
        console.log("button is clicked"+ text);
        let newText = text.toLowerCase();
        setText(newText)
        props.showAlert("Converted to lower case","success");
        speakMessage("Converted to lower case")

        
    }
    const handleClearClick = ()=>{
        localStorage.setItem(1,text)
        let newText = ("");
        setText(newText)
        window.speechSynthesis.cancel();
        props.showAlert("Text cleared","success");
        speakMessage("Text cleared")
        
    }
    const undo = ()=>{
        let item= localStorage.getItem(1);
        setText(item);
        speakMessage("text in undo now")

    }
    const removeExtraSpaces = () => {
        const clean = text.replace(/\s+/g, ' ').trim();
        setText(clean);
        props.showAlert("Extra spaces removed","success");
        speakMessage("Extra spaces removed")
      };
    
    
    const handleCopy = () => {
        navigator.clipboard.writeText(text);
        props.showAlert("Text copied to clipboard","success");
        speakMessage("text copied to clip board")

      }
  
    
    const speak = () => {
        let msg = new SpeechSynthesisUtterance();
        msg.text = text;
        window.speechSynthesis.speak(msg);
      }
      
    const handleOnChange=(event)=>{
        console.log("handle is clicked");
        setText(event.target.value)
    }
    const [text,setText] = useState('');

    return (
        <>
        <div className='container' style={{color:props.mode==='dark'?'white':'black'}}>
            <h1>{props.heading}</h1>

            <div className="mb-3">
                <textarea className="form-control" onChange={handleOnChange} style={{backgroundColor: props.mode=='dark'?'black':'white',color:props.mode=='dark'?'white':'black' }} value={text} id="textarea" rows="7"></textarea>
            </div>
            <button className="btn btn-primary mx-2" onClick={handleUpperClick}>Convert to Uppercase</button>
            <button className="btn btn-primary mx-2" onClick={handleLoClick}>Convert to Lowercase</button>
            <button className="btn btn-primary mx-2" onClick={handleClearClick}>Clear</button>
            <button className="btn btn-primary mx-2" onClick={undo}>Undo</button>
            <button className="btn btn-primary mx-2" onClick={removeExtraSpaces}>Remove spaces</button>
            <button className="btn btn-primary mx-2" onClick={handleCopy}>Copy text</button>
            <button className="btn btn-primary mx-2" onClick={speak}>Speak</button>
        </div>
        <div className="container" style={{color:props.mode==='dark'?'white':'black'}}>
            <h3>Your text summary</h3>
            <p><b>{text.split(" ").length-1}</b> words and <b>{text.length}</b> characters</p>
            <p>This text can be read in <b>{0.08*text.split(" ").length}</b> minutes.</p>
            <h3>Preview</h3>
            <p>{text.length>0?text:"Enter your text to preview it here"}</p>
        </div>
        </>


    )
}
