import React from 'react'
import PropTypes from 'prop-types'
// import {Link} from 'react-router-dom'



export default function Navbar(props) {
  return (
    <div>
      <nav className={`navbar navbar-expand navbar-${props.mode} lg bg-${props.mode}`}>
        <div className="container-fluid">
          <a className="navbar-brand" href="/">{props.title}</a>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <a className="nav-link active" aria-current="page" href="#">Home</a>
              </li>
{/* 
              <li className="nav-item">
                <Link className="nav-link active" aria-current="page" to="/About">{props.about}</Link>
              </li> */}

            </ul>

            <div className="htmlForm-check form-switch ">
              <input className="form-check-input mx-2 px-2" onClick={props.toggleMode} type="checkbox" role="switch" id="flexSwitchCheckDefault"/>
                <label className={`form-check-label mx-2  text-${props.mode=='light'?'dark':'light'}`} htmlFor="flexSwitchCheckDefault">Enable Darkmode</label>
            </div>
         
        </div>
        </div>
      </nav>
    </div>
  )
}

Navbar.propTypes = {
  title: PropTypes.string,

}

Navbar.defaultProps = {
  title: 'Your title here',
  about: 'text here'
};